import React from 'react';
import { Section, Container, Heading } from '../../globalStyle';
import {     
    AboutHead,    
    AboutHeadingSub,
    AboutDesc,
    AboutImgWrapper,
    AboutTextWrapper,   
    AboutSubtitle,
    AboutText,
    AboutTech,
    AboutTechInfo
} from './About.elements';

const TechData = [
    {
        name: 'HTML5',
        img: require('../../images/icons8-html-5-48.png').default
    },
    {
        name: 'CSS3',
        img: require('../../images/icons8-css3-48.png').default
    },
    {
        name: 'Javascript',
        img: require('../../images/icons8-javascript-48.png').default
    },
    {
        name: 'React',
        img: require('../../images/icons8-react-100.png').default
    },
    {
        name: 'Redux',
        img: require('../../images/icons8-redux-48.png').default
    },
    {
        name: 'Node.js',
        img: require('../../images/icons8-nodejs-48.png').default
    },
    {
        name: 'Firebase',
        img: require('../../images/icons8-firebase-48.png').default
    },
    {
        name: 'Jquery',
        img: require('../../images/icons8-jquery-50.png').default
    },
];

const About = () => {
    return (
        <>
        <Section id="ABOUT">
            <Container>
                <AboutHead>
                    <Heading>About</Heading>
                    <AboutHeadingSub>
                           
                    </AboutHeadingSub>
                </AboutHead>
                <AboutDesc>
                    <AboutImgWrapper>
                        <img src={require('../../images/about.jpg').default} alt=""/>
                    </AboutImgWrapper>
                    <AboutTextWrapper>                        
                        <AboutSubtitle> Motto</AboutSubtitle>
                        <AboutText>"    !"</AboutText>
                        <AboutSubtitle> Motivation</AboutSubtitle>
                        <AboutText>   AutoHotkey                    .      Node.js  <strong>Express.js</strong> <strong>Nest.js</strong>     <strong>Next.js</strong>  .</AboutText>
                        <AboutSubtitle> Skills</AboutSubtitle>
                        <AboutTech>
                            {TechData.map((item, i) => {
                                return (
                                    <AboutTechInfo key={i}>
                                        <img src={item.img}/> {item.name}
                                    </AboutTechInfo> 
                                )                         
                            })}                            
                        </AboutTech>
                </AboutTextWrapper>
                </AboutDesc>                   
            </Container>
        </Section>   
        </>
    );
};

export default About;