import React from 'react';
import { Container } from '../../globalStyle';
import {
    WorkWrapper,
    WorkHead,
    ProjectWrapper,
    Project,
    ProjectImg,    
    ProjectTextWrapper,
    ProjectTitle,
    ProjectDesc,
    ProjectTech,
    ProjectTechInfo,
    ProjectBtn,
    IconStyle
} from './Work.elements';
import { FaGithub, FaChrome } from "react-icons/fa";

const projectData = {
    netflix: {
        tech: ['React', 'Context API', 'axios', 'Firebase', 'React-Slick'],
        txt: ' ,  ,  ,        Firebase  , ,      DB .     API axios       , ,    DB     .          .      Context API .',
        url: require('../../images/project01-stream.jpg').default
    },
    loaya: {
        tech: ['React', 'Context API', 'axios', 'Node.js', 'Express.js', 'cheerio', 'heroku'],
        txt: '    API            (cheerio   )    API     . proxy, cors          .',
        url: require('../../images/project02-home.jpg').default
    }
};




const Work = () => {    
    return (
        <>
        <WorkWrapper id="WORK">
            <Container>                
                <WorkHead>Work</WorkHead>
                <ProjectWrapper>
                    <Project primary={true}>
                        <ProjectImg data={projectData.netflix} />                            
                        
                        <ProjectTextWrapper primary={true}>
                            <ProjectTitle><strong>Raflix</strong> (  )</ProjectTitle>
                            <ProjectTech>
                                {projectData.netflix.tech.map((item, i) => {
                                    return <ProjectTechInfo key={i}>{item}</ProjectTechInfo>
                                })}
                            </ProjectTech>
                            <ProjectDesc >
                             ,  ,  ,        Firebase  , ,      DB .     API axios       , ,    DB     .          .      Context API .
                            </ProjectDesc>
                            <ProjectBtn primary={true}>
                                <a target="_blank" href="https://github.com/ranjafunc/netflix_clone" alt=""><FaGithub style={IconStyle} />Get Code</a>                                
                            </ProjectBtn>
                            <ProjectBtn primary={false}>
                                <a target="_blank" href="https://ranjafunc.github.io/netflix_clone/" alt=""><FaChrome style={IconStyle} />Web</a>                              
                            </ProjectBtn>
                        </ProjectTextWrapper>
                    </Project>
                    <Project primary={false}>
                        <ProjectImg data={projectData.loaya} />                            
                        
                        <ProjectTextWrapper>
                            <ProjectTitle><strong>LOAYA</strong> (     )</ProjectTitle>
                            <ProjectTech>
                                {projectData.loaya.tech.map((item, i) => {
                                    return <ProjectTechInfo key={i}>{item}</ProjectTechInfo>
                                })}
                            </ProjectTech>
                            <ProjectDesc primary={false}>{projectData.loaya.txt}</ProjectDesc>          
                            
                            <ProjectBtn primary={true}>
                                <a target="_blank" href="https://github.com/ranjafunc/loa-status-app" alt=""><FaGithub style={IconStyle} />Get Code</a>                                
                            </ProjectBtn>
                            <ProjectBtn primary={false}>
                                <a target="_blank" href="https://loa-status-app.herokuapp.com/" alt=""><FaChrome style={IconStyle} />Web</a>                              
                            </ProjectBtn>
                        </ProjectTextWrapper>
                    </Project>
                    
                </ProjectWrapper>
                
            </Container>
        </WorkWrapper>   
        </>
    );
};

export default Work;
                    